"use client";

import { useState, useMemo, useEffect } from "react";
import RankingsTable from "@/components/RankingsTable";
import FixtureInput from "@/components/FixtureInput";
import FixtureList from "@/components/FixtureList";
import FixtureWithOutcomes from "@/components/FixtureWithOutcomes";
import { Fixture, CalculatedRanking, Ranking } from "@/types";
import { sampleRankings, allTeams } from "@/lib/data";
import { applyFixtures } from "@/lib/rankings";
import { fetchRankings, fetchFixtures, WRRankingsResponse } from "@/lib/api";

export default function Home() {
  const [userFixtures, setUserFixtures] = useState<Fixture[]>([]);
  const [completedFixtures, setCompletedFixtures] = useState<Fixture[]>([]);
  const [upcomingFixtures, setUpcomingFixtures] = useState<Fixture[]>([]);
  const [rankings, setRankings] = useState<Ranking[]>(sampleRankings);
  const [rankingsTime, setRankingsTime] = useState<number>(Date.now());
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // Fetch live rankings and fixtures on mount
  useEffect(() => {
    const loadData = async () => {
      try {
        setLoading(true);
        setError(null);

        // Fetch rankings
        const rankingsData = await fetchRankings();
        setRankings(rankingsData);

        // Get rankings effective time from API
        const response = await fetch("/api/rankings");
        const data: WRRankingsResponse = await response.json();
        const effectiveTime = new Date(data.effectiveTime).getTime();
        setRankingsTime(effectiveTime);

        // Fetch fixtures (only for ranked teams)
        const rankedTeamIds = new Set(rankingsData.map(r => r.team.id));
        const fixturesData = await fetchFixtures(effectiveTime, rankedTeamIds);

        // Split into completed (with scores) and upcoming (without scores)
        const now = Date.now();
        const oneWeekFromNow = now + (7 * 24 * 60 * 60 * 1000);

        const completed = fixturesData.filter(
          f => f.homeScore !== null && f.awayScore !== null
        );
        const upcoming = fixturesData.filter(
          f => (f.homeScore === null || f.awayScore === null) &&
               new Date(f.date || 0).getTime() >= now &&
               new Date(f.date || 0).getTime() <= oneWeekFromNow
        );

        setCompletedFixtures(completed);
        setUpcomingFixtures(upcoming);
        setLoading(false);
      } catch (err) {
        console.error("Error loading data:", err);
        setError("Failed to load live data. Using sample data.");
        setLoading(false);
      }
    };

    loadData();
  }, []);

  // Calculate updated rankings based on completed + user fixtures
  const calculatedRankings: CalculatedRanking[] = useMemo(() => {
    const allFixtures = [...completedFixtures, ...userFixtures];
    return applyFixtures(rankings, allFixtures);
  }, [rankings, completedFixtures, userFixtures]);

  // Original rankings as CalculatedRanking (no changes)
  const originalRankings: CalculatedRanking[] = useMemo(() => {
    return rankings.map((r) => ({
      ...r,
      change: 0,
      positionChange: 0,
    }));
  }, [rankings]);

  const handleAddFixture = (fixture: Fixture) => {
    setUserFixtures([...userFixtures, fixture]);
  };

  const handleRemoveFixture = (id: string) => {
    setUserFixtures(userFixtures.filter((f) => f.id !== id));
  };

  const handleClearUserFixtures = () => {
    setUserFixtures([]);
  };

  // Show calculated rankings if there are any completed fixtures or user predictions
  const hasChanges = completedFixtures.length > 0 || userFixtures.length > 0;
  const displayRankings = hasChanges ? calculatedRankings : originalRankings;

  // Get set of added fixture IDs for upcoming fixtures display
  const addedFixtureIds = useMemo(() => {
    return new Set(userFixtures.map((f) => f.id));
  }, [userFixtures]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 via-green-50 to-gray-50 dark:from-gray-900 dark:via-gray-800 dark:to-gray-900">
      {/* Header */}
      <header className="bg-white dark:bg-gray-800 shadow-md border-b-4 border-green-600">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <h1 className="text-4xl font-bold text-gray-900 dark:text-white">
            World Rugby Rankings Calculator
          </h1>
          <p className="mt-2 text-gray-600 dark:text-gray-400">
            Calculate how match results affect World Rugby rankings
          </p>
          {loading && (
            <p className="mt-2 text-sm text-blue-600 dark:text-blue-400">
              Loading live data from World Rugby...
            </p>
          )}
          {error && (
            <p className="mt-2 text-sm text-yellow-600 dark:text-yellow-400">
              {error}
            </p>
          )}
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Left Column - Rankings */}
          <div className="space-y-6">
            {/* Info Banner */}
            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-4 border border-gray-200 dark:border-gray-700">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-600 dark:text-gray-400">
                    {completedFixtures.length > 0 && (
                      <span className="font-medium text-green-600 dark:text-green-400">
                        {completedFixtures.length} completed match{completedFixtures.length !== 1 ? 'es' : ''} applied
                      </span>
                    )}
                    {completedFixtures.length > 0 && userFixtures.length > 0 && " ‚Ä¢ "}
                    {userFixtures.length > 0 && (
                      <span className="font-medium text-blue-600 dark:text-blue-400">
                        {userFixtures.length} prediction{userFixtures.length !== 1 ? 's' : ''}
                      </span>
                    )}
                    {!hasChanges && (
                      <span>No matches applied yet</span>
                    )}
                  </p>
                </div>
                {userFixtures.length > 0 && (
                  <button
                    onClick={handleClearUserFixtures}
                    className="px-4 py-2 text-sm font-medium text-red-600 dark:text-red-400 hover:bg-red-50 dark:hover:bg-red-900/20 rounded-lg transition-colors"
                  >
                    Clear Predictions
                  </button>
                )}
              </div>
            </div>

            {/* Rankings Table */}
            <RankingsTable
              rankings={displayRankings}
              title={hasChanges ? "Calculated Rankings" : "Official Rankings"}
            />
          </div>

          {/* Right Column - Fixtures */}
          <div className="space-y-6">
            {/* Completed Matches */}
            {!loading && completedFixtures.length > 0 && (
              <FixtureList
                fixtures={completedFixtures}
                onRemoveFixture={() => {}}
                title="Completed Matches"
                readonly
              />
            )}

            {/* User Predictions */}
            {userFixtures.length > 0 && (
              <FixtureList
                fixtures={userFixtures}
                onRemoveFixture={handleRemoveFixture}
                title="Your Predictions"
              />
            )}

            {/* Upcoming Fixtures */}
            {!loading && upcomingFixtures.length > 0 && (
              <div className="space-y-4">
                <h2 className="text-2xl font-bold text-gray-800 dark:text-gray-100">
                  Upcoming Fixtures ({upcomingFixtures.length})
                </h2>
                {upcomingFixtures.map((fixture) => (
                  <FixtureWithOutcomes
                    key={fixture.id}
                    fixture={fixture}
                    rankings={rankings}
                    onAddFixture={handleAddFixture}
                    isAdded={addedFixtureIds.has(fixture.id)}
                  />
                ))}
              </div>
            )}
          </div>
        </div>

        {/* Footer */}
        <footer className="mt-12 text-center text-sm text-gray-500 dark:text-gray-400">
          <p>
            Rankings calculation based on{" "}
            <a
              href="https://www.world.rugby/tournaments/rankings/explanation"
              target="_blank"
              rel="noopener noreferrer"
              className="text-green-600 dark:text-green-400 hover:underline"
            >
              World Rugby methodology
            </a>
          </p>
          <p className="mt-2">
            Data from{" "}
            <a
              href="https://www.world.rugby/rankings"
              target="_blank"
              rel="noopener noreferrer"
              className="text-green-600 dark:text-green-400 hover:underline"
            >
              World Rugby
            </a>
            {" ‚Ä¢ "}
            Inspired by{" "}
            <a
              href="https://rawling.github.io/wr-calc/"
              target="_blank"
              rel="noopener noreferrer"
              className="text-green-600 dark:text-green-400 hover:underline"
            >
              rawling/wr-calc
            </a>
          </p>
        </footer>
      </main>
    </div>
  );
}
